body.users-php{
  #wpbody{
    #wpbody-content{
      .wrap{
        form{
          .search-box{
            @include breakpoint( 'minmedium' ){
              width: 100%;
              padding: 20px 0;
              display: flex;
              #user-search-input{
                width: 100%;
              }
            }
            
          }
          table{
            .column-username{
              img{
                border-radius: 16px;;
              }
            }
          }
          .tablenav.bottom{
            display: none;
          }
        }
      }
    }
  }
}

body.profile-php{
  .user-profile-picture{
    td{
      img{
        border-radius: 50px;
      }
    }
  }
}

.defaultavatarpicker{
  .avatar{
    border-radius: 50px;
  }
}